import React, { lazy, Suspense, useEffect } from "react";
import { useLocation } from "react-router-dom";
import pageRoutes from "./pages/pageRoutes"; // Import the mapping

export let scenePath: string | null = null;

// Default fallback component
const DefaultPage = lazy(() => import("./pages/npgDemo/App"));

const Routing: React.FC = () => {
	const location = useLocation();
	let path = location.pathname.replace(/^\/+/, ""); // Remove leading "/"

	useEffect(() => {
		scenePath = `/cube-test/projects/${path || "npgDemo"}`;
	}, [path]);

	// Check if the path exists in our mapping
	const PageComponent = lazy(pageRoutes[path] || pageRoutes["npgDemo"]);

	if (!path) {
		window.history.replaceState(null, "", "/cube-test/npgDemo");
	}

	return (
		<Suspense fallback={<div>Loading...</div>}>
			<PageComponent />
		</Suspense>
	);
};

export default Routing;
