import { createTheme } from "@mui/material/styles";
import RedHatDisplay from "/fonts/RedHatDisplay-Regular.ttf";
import RedHatDisplaySemibold from "/fonts/RedHatDisplay-SemiBold.ttf";

const theme = createTheme({
  typography: {
    fontFamily: "Red Hat Display, Arial, sans-serif",
    h1: {
      fontFamily: "Red Hat Display",
      fontWeight: 600,
    },
    h2: {
      fontFamily: "Red Hat Display",
      fontWeight: 600,
    },
    h3: {
      fontFamily: "Red Hat Display",
      fontWeight: 600,
    },
    h4: {
      fontFamily: "Red Hat Display",
      fontWeight: 600,
    },
    h5: {
      fontFamily: "Red Hat Display",
      fontWeight: 600,
    },
    h6: {
      fontFamily: "Red Hat Display",
      fontWeight: 600,
    },
    subtitle1: {
      fontFamily: "Red Hat Display",
      fontWeight: 400,
    },
    subtitle2: {
      fontFamily: "Red Hat Display",
      fontWeight: 400,
    },
    body1: {
      fontFamily: "Red Hat Display",
      fontWeight: 400,
    },
    body2: {
      fontFamily: "Red Hat Display",
      fontWeight: 400,
    },
    button: {
      fontFamily: "Red Hat Display",
      fontWeight: 600,
    },
    caption: {
      fontFamily: "Red Hat Display",
      fontWeight: 400,
    },
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        "@font-face": [
          {
            fontFamily: "Red Hat Display",
            fontStyle: "normal",
            fontWeight: 400,
            src: `local('Red Hat Display'), local('RedHatDisplay-Regular'), url(${RedHatDisplay}) format('truetype')`,
          },
          {
            fontFamily: "Red Hat Display",
            fontStyle: "normal",
            fontWeight: 600,
            src: `local('Red Hat Display'), local('RedHatDisplay-SemiBold'), url(${RedHatDisplaySemibold}) format('truetype')`,
          },
        ],
      },
    },
  },
  palette: {
    primary: {
      main: "#ffffff",
    },
    secondary: {
      main: "#000000",
    },
  },
});

export default theme;
